
/* FONTS */
@import url(https://fonts.googleapis.com/css?family=Oswald:400,300,700);
@import url(https://fonts.googleapis.com/css?family=Roboto+Slab:400,100,300,700);

body {
  font-family: 'Roboto Slab', serif;
  font-weight: 400;
}

h1, h2, h3, h4, h5, h6 {
  font-family: 'Oswald', sans-serif;
  font-weight: 400;
}


/* BASE */
* {
  padding: 0;
  margin: 0;
}

ul, li {
  list-style: none;
}

a {
  text-decoration: none;
  color: $black;

  &:active {
    color: $black;
  }
}

.main {
  margin: 0 auto;
  max-width: 750px;
}

h1, h2 {
  padding: 20px 0px;
  text-align: center;
}

h3 {
  font-size: 2rem;
}

/* FORMS */
select {
  margin-left: 10px;
}


/* UTILITY */
.u-is-hidden {
	display: none;
}

.u-float {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: justify;
	    justify-content: space-between;

	.u-float__item {
		-ms-flex: 1 1;
		    flex: 1 1;
	}
}

.u-is-highlight {
	background-color: orange;
}




