.bookList__title {
	margin-top: 20px;
	border-top-left-radius: 2px;
  border-top-right-radius: 2px;
  background: #607D8B;
  color: white;
  padding: 15px;
}

section {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: justify;
	    justify-content: space-between;
	background: #95a5a6;
  padding: 7px 10px;
}

.bookList__list {
	padding: 0 10px;
  border: 1px solid #95a5a6;
  border-top: transparent;
  padding-top: 50px;
}



